// 导出一个router实例
import VueRouter from 'vue-router'
import Vue from 'vue'

import Hot from '@/views/Hot'
import Detail from '@/views/Detail'

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    {path:'/',redirect:'/hot'},
    {path:'/hot', component: Hot},
    {path:'/detail/:id', component: Detail}
  ]
})

export default router